  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    sans-serif;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.new-game-btn {
  border: 1px solid oklch(87.2% 0.01 258.338);
  background: white;
  border-radius: 5px;
  padding: 0.75rem 1rem;
  font-weight: bold;
  cursor: pointer;
}

.new-game-btn:hover,
.new-game-btn:focus-visible {
  background-color: oklch(97% 0.014 254.604);
  outline: 2px solid oklch(70.7% 0.165 254.624);
  outline-offset: -2px;
}

.status {
  margin: 1rem 0;
  padding: 1rem;
  border-radius: 5px;
  background: oklch(96.7% 0.003 264.542);
  border: 1px solid oklch(87.2% 0.01 258.338);
}

.boards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  align-items: start;
}

.board {
  display: grid;
  grid-template-columns: repeat(10, 32px);
  grid-template-rows: repeat(10, 32px);
  gap: 2px;
}

.cell {
  border: 1px solid oklch(87.2% 0.01 258.338);
  background: white;
  width: 32px;
  height: 32px;
  font-size: 1rem;
  font-weight: bold;
}

.cell:disabled {
  cursor: default;
}

.board .cell:not(:disabled) {
  cursor: pointer;
}

.board .cell:not(:disabled):hover,
.board .cell:focus-visible {
  background-color: oklch(97% 0.014 254.604);
  outline: 2px solid oklch(70.7% 0.165 254.624);
  outline-offset: -2px;
}

.cell--ship {
  background: oklch(70.7% 0.165 254.624);
  border-color: oklch(62.3% 0.214 259.815);
}

.cell--hit {
  border-color: oklch(63.7% 0.237 25.331);
  background: oklch(70.4% 0.191 22.216);
  color: white;
}

.cell--hit::after {
  content: "✕";
}

.cell--miss {
  border-color: oklch(87.2% 0.01 258.338);
  background: oklch(96.7% 0.003 264.542);
  color: oklch(70.7% 0.022 261.325);
}

.cell--miss::after {
  content: "·";
}
